/*Landing page */
.landingpage{
    height: 10em;
    position: relative 
}
.landingpage span{
    background: rgba(0, 0, 0, 0.75);
    width: 50px;
    height: 300px;
}

.landingpage .title, .subtitle{
    font-family: 'Miller Display Semibold';
    color: #fff; 
    font-size: 50px;
    font-weight: 570;
    text-shadow: 2px 2px 100px #555555;
    margin-bottom: 0%;
    padding-bottom: 0%;
    display: inline-block;
}

.landingpage .title{
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%) 
}

.subtitle{
    font-size: 45px;
    font-weight: 500;
    margin: 0;
    position: absolute;
    top: 57.4%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%) 
}

.bg {
    background-image: url("./img/landingimg.jpg");
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

